import './App.css';
import React, { useEffect, useState } from 'react';
import axios from 'axios';
import { Line } from 'react-chartjs-2';
import {
    Chart as ChartJS,
    CategoryScale,
    LinearScale,
    PointElement,
    LineElement,
    Title,
    Tooltip,
    Legend,
} from 'chart.js';

// Chart.js     
ChartJS.register(
    CategoryScale,
    LinearScale,
    PointElement,
    LineElement,
    Title,
    Tooltip,
    Legend
);

function App() {
    const [hello, setHello] = useState('')
    const [emoteCounts, setEmoteCounts] = useState({})

    let ary = ['  ', '  ', '']
    let [titles, setTitles] = useState(ary)

    useEffect(() => {
        axios.get('/api/admin/hello')
            .then(response => setHello(response.data))
            .catch(error => console.log(error))

        axios.get('/api/admin/emoteCounts')
            .then(response => setEmoteCounts(response.data))
            .catch(error => console.log(error))
    }, [])

    const sortTitles = () => {
        const sortedTitles = [...titles].sort();
        setTitles(sortedTitles);
    };

    return (
        <div className="App">
            <div className="black-nav">
                <h4>  .</h4>
            </div>

            <button onClick={sortTitles}>  </button>

            {titles.map((title, index) => (
                <ProductList key={title} name={title} date="11 26" />
            ))}

            <h4>  ~~ : {hello}</h4>
            <div style={{ width: '80%', margin: '0 auto' }}>
                <EmoteCountChart data={emoteCounts} />
            </div>
        </div>
    );
}

const ProductList = (
    { name },
    { date }
) => {
    const [like, setLike] = useState(0);

    return (
        <div className="list">
            <h4>
                {name}
                <span onClick={() => {
                    setLike(like + 1)
                }}>  </span>
                {like}
            </h4>
            <p>
                {date} 
            </p>
        </div>
    );
}

function EmoteCountChart({ data }) {
    const chartData = {
        labels: Object.keys(data), //  ()
        datasets: [
            {
                label: 'Emote Counts', //  
                data: Object.values(data), //   ()
                borderColor: 'rgba(75, 192, 192, 1)', //  
                backgroundColor: 'rgba(75, 192, 192, 0.2)', //  
                tension: 0.0, //  
            },
        ],
    };

    const options = {
        responsive: true,
        plugins: {
            legend: { position: 'top' },
            title: { display: true, text: 'Emote Counts Over Time' },
        },
        scales: {
            x: { title: { display: true, text: 'Date' } },
            y: { title: { display: true, text: 'Counts' } },
        },
    };

    return <Line data={chartData} options={options} />;
}



export default App;